import { json } from "@remix-run/node";
import { Form, Link, useActionData,useLoaderData, useSearchParams } from "@remix-run/react";
import * as React from "react";
import HeaderFile from "~/components/HeaderFile";
import Footer from '~/components/Footer';
import { toast } from "react-toastify";
import verifyAuthenticator from "~/services/verify.server";
import { verifySessionStorage } from "~/services/verifySession.server";
import Cookies from 'js-cookie'
import { getDirectusClient } from '~/lib/directus'
import { getAssetURL } from "../utils/get-asset-url";

export const loader = async ({ request }) => {
    await verifyAuthenticator.isAuthenticated(request, {
        successRedirect: "/verification"
    });
    const session = await verifySessionStorage.getSession(
        request.headers.get("Cookie")
    );
    var cookie = request.headers.get("Cookie");
    const error = session.get("sessionErrorKey");
    const directus = await getDirectusClient()
    const Location1 = await directus.items('Locations').readByQuery({
        fields: ['logo'],
        filter: {id:1},
      })?.then(e=>e?.data?.[0]?.logo)
    return json({ error, session, cookie,Location1 });
};

export const action = async ({ request, context }) => {
    // call my authenticator
    const resp = await verifyAuthenticator.authenticate("form", request, {
        successRedirect: "/verification",
        failureRedirect: "/login",
        throwOnError: true,
        context,
    });
    return resp;
};

export function meta() {
    return { title: "Login" };
}
export default function LoginPage() {
    const loaderData = useLoaderData();
    const actiondata=useActionData();
    const [isVisible, setVisible] = React.useState(false);
    const [emaildata, setEmaildata] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [buttonTrigger,setButtonTrigger]=React.useState(false)
    const emailRef=React.useRef(null);
    const passwordRef=React.useRef(null);

    React.useEffect(() => {
        Cookies.remove('_session');
        const element = document.getElementById("submitId");
        element.addEventListener("click", function() {
            if(!buttonTrigger){
                if(!!emaildata?.trim() && !!password?.trim()){
                    if (document.activeElement !== emailRef.current && document.activeElement !== passwordRef.current) {
                    element.style.pointerEvents="none";
                    setButtonTrigger(true);
                    }
                }
                
            }
          });
        if (loaderData?.error) {
            if (emaildata && password) {
                if (document.activeElement !== emailRef.current && document.activeElement !== passwordRef.current) {
                toast.error(loaderData?.error?.message,{
                    onClose:()=>{element.style.pointerEvents="auto";setButtonTrigger(false)}
                });
            }
                
            }
        
        }
        return ()=>{
            element.removeEventListener("click", function() {
                 setButtonTrigger(false);
              })
        }
    }, [loaderData?.error,emaildata,password]);

    return (
        <>
            <HeaderFile login logoCenter={true}/>
            <div className="main-content-area">
                <div className="flex justify-center h-[calc(100vh-75px)] items-center px-5">
                    <Form
                        className="max-w-[400px] w-full flex flex-col items-center text-[14px] "
                        method="post"
                    >
                       <span className="flex w-full justify-between mb-12 items-center">
                        <h1 className="text-left text-[24px] text-black ">
                            Login Account
                        </h1>
                        <img src={loaderData?.Location1?getAssetURL(loaderData?.Location1):''} alt="Logo" className="min-w-[110px]"/>
                       </span>
                        <div className="mb-6 w-full">
                            <label
                                htmlFor="u-mail-id1"
                                className="block text-sm font-bold text-primary"
                            >
                                Email
                            </label>
                            <input
                                type="email"
                                id="u-mail-id1"
                                name="email"
                                ref={emailRef}
                                placeholder="Your@email.dk"
                                value={emaildata}
                                onChange={(e) => {
                                    setEmaildata(e.target.value) 
                                    setButtonTrigger(false)
                                    document.getElementById("submitId").style.pointerEvents="auto"
                                    }
                                }
                                required
                                className="mt-2 w-full px-4 py-3 flex justify-center items-center text-primary shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                            />
                        </div>
                        <div className="mb-3 w-full relative">
                            <label
                                htmlFor="u-password-1"
                                className="block text-sm font-bold text-primary"
                            >
                                Password
                            </label>
                            <input
                                type={!isVisible ? "password" : "text"}
                                id="u-password-1"
                                placeholder="Password"
                                ref={passwordRef}
                                name="password"
                                required
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                className="mt-2 w-full px-4 py-3 flex justify-center items-center text-primary shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                            />

                            <div className="show-password Password__icon absolute z-20 right-5 bottom-[8px] cursor-pointer text-gray-300">
                                {!isVisible ?
                                    (
                                        <button
                                            id="eye-off"
                                            click="show=true"
                                            type="button"
                                            className="show === true ? 'hidden':'inline-block'"
                                            onClick={() => {
                                                setVisible(!isVisible);
                                            }}
                                        >
                                            <svg
                                                xmlns="http://www.w3.org/2000/svg"
                                                width="24"
                                                height="24"
                                                viewBox="0 0 24 24"
                                                fill="none"
                                                stroke="currentColor"
                                                strokeWidth="2"
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                className="feather feather-eye-off w-5 h-5 text-[#bbb]"
                                            >
                                                <path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19m-6.72-1.07a3 3 0 1 1-4.24-4.24"></path>
                                                <line x1="1" y1="1" x2="23" y2="23"></line>
                                            </svg>
                                        </button>
                                    ) : (
                                        <button
                                            id="eye-on"
                                            click="show=false"
                                            type="button"

                                            className="show === false ? 'hidden':'inline-block'"
                                            onClick={() => {
                                                setVisible(!isVisible);
                                            }}
                                        >
                                            <svg
                                                xmlns="http://www.w3.org/2000/svg"
                                                viewBox="0 0 24 24"
                                                fill="none"
                                                stroke="currentColor"
                                                strokeWidth="2"
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                className="feather feather-eye w-5 h-5 text-[#bbb]"
                                            >
                                                <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                                                <circle cx="12" cy="12" r="3"></circle>
                                            </svg>
                                        </button>
                                    )}
                            </div>
                        </div>
                        <Link
                            to={"/forgetpassword"}
                            className="self-end text-right font-semibold text-primary"
                        >
                            Forgot password?
                        </Link>
                        <div className="mt-12 w-full">
                            <button
                                type="submit"
                                name="myButton"
                                id="submitId"
                                className="inline-flex w-full items-center justify-center h-[50px] text-[18px] rounded border bg-primary border-primary px-4 py-2 text-sm font-bold text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
                            >
                                {buttonTrigger?"Logging In...":"Log In"}
                            </button>
                        </div>
                    </Form>
                </div>
            </div>
            <Footer login/>
        </>
    );
}